import React from 'react'
import { properties } from '../../Properties/Properties.js'
import BaseSurveyComponent from "../BaseSurveyComponent"
import CategoriesComponent from "../CategoriesComponent"
import MainMenuComponent from "../MainMenuComponent"
import MapChart from '../MapChart.js'
import { useParams } from "react-router-dom";
import { PopupContext } from '../../index'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import IconButton from '@material-ui/core/IconButton';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Collapse from '@material-ui/core/Collapse';
import Button from '@material-ui/core/Button';
import KeyboardIcon from '@material-ui/icons/Keyboard';

import Grid from '@material-ui/core/Grid';
import Dialog from '@material-ui/core/Dialog';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles((theme) => ({
    expand: {
        transform: 'rotate(0deg)',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
}))

export const KeyPressedContext = React.createContext(null)

export default function RandomComponent(props) {
    const setMessageFunc = React.useContext(PopupContext)
    const classes = useStyles()

    const [state, setState] = React.useState({
        isLoading: true,
        survey: {},
        expanded: false,
        keyPressed: null,
        commentsExpanded: false,
        imageOpen: false
    })

    const getRandom = e => {
        setState({ ...state, isLoading: true, keyPressed: null });
        fetch(`${properties.address}/getRandom${window.location.search}`, { method: 'GET', credentials: 'include' })
            .then(response => {
                if (response.status === 200)
                    return response.json()
            })
            .then(result => {
                setState({ ...state, survey: result, isLoading: false })
                if (result == null)
                    setMessageFunc("   :(")
            })
    }
    const onSurveyDeleteFunc = e => {
        getRandom()
    }
    const handleExpandMapClick = e => {
        console.log(state.expanded)
        setState({ ...state, expanded: !state.expanded })
    }

    const handleClose = opened => {
        setState({ ...state, hintOpen: opened })
    }

    React.useEffect(() => {
        getRandom()
    }, [props.location.search])

    window.onkeyup = e => {
        console.log(e.keyCode)
        if (e.keyCode === 32) {
            getRandom()
            return
        }
        if (e.keyCode === 77) {
            setState({ ...state, expanded: !state.expanded })
            return
        }
        if (e.keyCode === 67) {
            setState({ ...state, commentsExpanded: !state.commentsExpanded })
            return
        }
        if (e.keyCode === 73) {
            setState({ ...state, imageOpen: !state.imageOpen })
            return
        }
        if (e.keyCode === 75) {
            setState({ ...state, hintOpen: !state.hintOpen })
            return
        }
        setState({ ...state, keyPressed: e })
    }

    return <KeyPressedContext.Provider value={state.keyPressed} >
        <Grid item sm={9} style={{ margin: "auto", marginTop: "20px", position: "relative" }}>
            <Card style={{ backgroundColor: "transparent" }} onClick={handleExpandMapClick}>
                <IconButton
                    color="primary"
                    style={{ padding: "0", margin: "0 auto", display: "block" }}
                    className={clsx(classes.expand, { [classes.expandOpen]: state.expanded, })}
                >
                    <ExpandMoreIcon />
                </IconButton>
            </Card>
            <Collapse timeout={100}
                style={{
                    borderTop: "1px solid #e0e0e0",
                    position: "absolute",
                    width: "100%",
                    zIndex: 1,
                }}
                in={state.expanded} unmountOnExit>
                <MapChart markers={state.survey != null && !state.isLoading && state.survey.locations} />
            </ Collapse>
            {state.survey != null && !state.isLoading && <BaseSurveyComponent imageOpen={state.imageOpen} expanded={state.commentsExpanded} openMap={handleExpandMapClick} bindKeys={true} surveyJson={state.survey} onSurveyDeleteFunc={onSurveyDeleteFunc} />}


            <Button variant="contained" color="primary" onClick={getRandom}></Button>
            {/* <Button variant="contained" color="primary" onClick={getRandom}> */}
            <Dialog
                open={state.hintOpen}
                onClose={() => { handleClose(false) }}
            >
                <IconButton style={{ position: "absolute", right: "0" }} onClick={() => { handleClose(false) }}>
                    <CloseIcon />
                </IconButton>
                {/* text */}
                <img alt="" style={{ width: "100%" }} src={"https://i.ibb.co/TMxXxjx/Keys.png"}></img>
            </Dialog>
            <IconButton onClick = {() => { handleClose(true) }} color="primary"><KeyboardIcon /></IconButton>
            {/* </Button> */}

        </Grid>
    </KeyPressedContext.Provider>
}